<template>
  <div>
    <BaseButton buttonName="测试"></BaseButton>1.'keep-alive' 缓存失活的组件,要求被切换到的组件都有自己的名字，不论是通过组件的 name 选项还是局部/全局注册
    2. input ref="input" 通过this.$refs.input 来获取input元素
    3.为扩展到更深层级的嵌套组件上,需要依赖注入，provide 和 inject 类似props
    4.异步加载组件 myComponent: () => import('./my-async-component')
    <br />5.过渡动画
    <el-button @click="show=!show">测试过度动画</el-button>
    <transition name="fade">
      <span v-if="show">hello</span>
    </transition>
  </div>
</template>
<script>
export default {
  name: "VueDeepKnowledge",
  data() {
    return {
      show: true
    };
  }
};
</script>
<style lang="scss">
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>


